.tg-menu-bar {
  /* background: white; */
  color: inherit;
  text-decoration: none;
  z-index: 19;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 34px;
  width: 100%;
  padding-left: 15px;
}

.tg-menu-bar-item {
  margin: 2px;
}

.tg-menu-bar .bp3-popover {
  margin-top: -1px !important;
}

.tg-menu-bar-popover .bp3-popover .bp3-popover-arrow {
  display: none;
}

.tg-menu-bar-popover .bp3-popover-wrapper {
  width: 100%;
}

li.bp3-menu-divider + li.bp3-menu-divider,
li.bp3-menu-divider:first-child,
li.bp3-menu-divider:last-child {
  display: none;
}
